
.info-box {
  position: absolute;
  width: 25%;
}
label {
  padding-right: 1em;
}
.selected {
  fill: #acdfb7;
}
.tree {
  margin: 0 auto;
  height: 950px;
  width: 1000px;
}
@media (max-width: 600px) {
.tree {
    height: 400px;
    width: 400px;
}
}
.treeclass .nodetree circle {
  r: 2;
  fill: rgba(50, 50, 180, 0.5);
}
.treeclass .node--internal circle {
  cursor: pointer;
  fill: #555;
  r: 0;
}
.link-active {
  opacity: 1;
  stroke: #000;
  stroke-width: 3px !important;
}
.treeclass .nodetree text {
  font: 10px sans-serif;
  cursor: pointer;
}
.treeclass .nodetree text:hover {
  font-weight: bold;
  background-color: #fff;
  display: block;
}
.treeclass .nodetree.selected text {
  font-weight: bold;
  font-size: 1em;
}
.treeclass .linktree {
  fill: none;
  stroke-opacity: 0.6;
  stroke-width: 1.5px;
}
th {
  cursor: pointer !important;
}
.positive {
  color: #acdfb7 !important;
}



